<template>
	<div class="personal-content-body">
		<div class="table-content">
			<div class="clear table-content-title">
				<div class="left">融资事件</div>

			</div>
			<!-- <div class="list-nav"><span @click="goBack('tuneUpAssistantList')">尽调助手&nbsp;&nbsp;></span>
        <span>行业近期投融资历史</span></div> -->

			<div class="investmentEvent-content-box">
				<div class="investmentEvent-term-box">
					<div class="term-line-box">
						<span class="term-line-box-left search-size">搜索</span>
						<div class="term-line-box-right">
							<input type="text" name="" id="" v-model="searchText" @keyup.enter="getInvestList(1)">

							<div class="search-btn" @click="toSearch">
								<img src="../../images/icon_search.png" alt="">
							</div>
							<span class="iconfont icon-delete-black" v-if="searchText!=''" @click="toClearSearchText"></span>
						</div>
					</div>
					<div class="term-line-box" v-for="(item,index) of dataArr " :key="index">
						<span class="term-line-box-left">{{item.termName}}:</span>
						<div class="term-line-box-right ">
							<span class="selcet-item" @click="termSearch('不限',index)" :class="{'selcet-item-click':termArr[index]=='不限'}">不限</span>
						</div>
						<div class="term-line-box-right term-line-box-right-2">
							<span class="selcet-item" v-for="(item1,index1) of item.termList" :key="index1" @click="termSearch(item1.category_name,index,item1.id)" :class="{'selcet-item-click':termArr[index]==item1.category_name}" v-if="index==0">{{item1.category_name}}</span>
							<span class="selcet-item" v-if="index==0" @click="toAlertMore()">更多>></span>
							<span class="selcet-item" v-for="(item1,index1) of item.termList" :key="index1" @click="termSearch(item1,index)" :class="{'selcet-item-click':termArr[index]==item1}" v-if="index==1">{{item1}}</span>
						</div>

					</div>
				</div>
				<div class="search-result-box">
					<div class="search-result-box-title">
						<span class="result-txt">共搜索到</span><span class="result-number">{{total}}</span><span class="result-txt">条结果</span>
						<span class="result-term" v-if="termArr[0]=='不限'&&termArr[1]=='不限'"></span>
						<span class="result-term" v-else>当前条件：</span>
						<div class="result-term-item" v-for="(item,index) of termArr" :key="index" @click="deleteTerm(index)" v-if="item!='不限'"><span>{{item}}</span> <img src="../../images/icon_delete1.png" alt=""></div>
						<!-- <div class="result-term-item"><span>2018</span> <img src="../../images/icon_delete1.png" alt=""></div> -->

					</div>
					<div class="search-result-box-items">
						<div class="search-result-box-item-title" v-if="total!=0">
							<div class="search-result-box-item-col search-result-box-item-szf"><span class="firstSpan">受资方</span></div>
							<div class="search-result-box-item-col search-result-box-item-hy"> <span class="firstSpan">所属行业</span></div>
							<div class="search-result-box-item-col search-result-box-item-je"> <span class="firstSpan">投资金额</span></div>
							<div class="search-result-box-item-col search-result-box-item-tzf"><span class="firstSpan">投资方</span></div>
							<!-- <div class="search-result-box-item-col search-result-box-item-xq"><span class="firstSpan1">详情</span></div> -->
						</div>
						<div class="search-result-box-item" v-for="(item,index) of dataList" :key="index" v-if="total!=0">
							<div class="search-result-box-item-col search-result-box-item-szf"><span class="firstSpan">{{item.invested_sname}}</span><span class="secondSpan">{{item.invest_time}}</span></div>
							<div class="search-result-box-item-col search-result-box-item-hy"><span class="firstSpan ">{{item.industry_name}}</span></div>
							<div class="search-result-box-item-col search-result-box-item-je"><span class=" firstSpan tablelink">{{item.money}}</span></div>
							<div class="search-result-box-item-col search-result-box-item-tzf">
								<span class=" firstSpan">

                            <span v-for="(item1,index1) of item.investor" :key="index1" class="firstSpan"  >{{index1!=0?"/":''}}{{item1}}</span>
								</span>
							</div>
						</div>
						<div class="table-nodata flex_center" v-if="total==0">
							<img src="../../images/table_nodata.png">
							<span>没有对应数据</span>
						</div>
					</div>
					<div class="pagination-normal" v-if="total!=0">
						<el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
						</el-pagination>
					</div>
				</div>
			</div>

		</div>
		<el-dialog title="选择行业分类" :visible.sync="addaccountDialogVisible1" width="880px" center :append-to-body='true' :before-close="toCancleConfirm1" class="investmentEvents_alert">
			<div class="dialog_content1">
				<div class="sort_level_1" v-for="(item,index) of sortList" :key="index">
					<div class="sort_level_2">
						<span class="sort_item sort_item_two_level" @click="termSearch(item.category_name,0,item.id,'alert')">[{{item.category_name}}]</span>
					</div>
					<div class="sort_level_2" v-for="(item1,index1) of item.child" :key="index1" v-if="item1.child.length>0">
						<span class="sort_item" @click="termSearch(item1.category_name,0,item1.id,'alert')">[{{item1.category_name}}]</span>
						<span class="sort_item" v-for="(item2,index2) of item1.child" :key="index2" @click="termSearch(item2.category_name,0,item2.id,'alert')">{{item2.category_name}}</span>
					</div>
					<div class="sort_level_2">
						<span class="sort_item" v-for="(item3,index3) of item.noChildArr" :key="index3" @click="termSearch(item3.category_name,0,item3.id,'alert')">{{item3.category_name}}</span>
					</div>
				</div>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import {
		invest_year,
		invest_category,
		investList
	} from "../../config/api";
	import {
		dialogConfirm,
		dialogConfirmHasbtn
	} from "../../config/mUtils";
	export default {
		data() {
			return {
				pageSize: 20,
				searchText: "", //搜索内容
				total: 0,
				currentPage: 1,
				//储存条件的数组
				termArr: ["不限", "不限"],
				dataArr: [{
						termName: "行业",
						termList: []
					},
					{
						termName: "时间",
						termList: []
					}
				],
				termId: "",
				dataList: [],
				addaccountDialogVisible1: false,
				sortList: [],
			};
		},
		created() {
			this.getInvestYear();
			this.getInvestCategory();
			this.getInvestList(1);
		},
		computed: {},
		mounted() {},
		methods: {
			//分页改变
			currentChange(val) {
				if(this.total > this.pageSize) {
					this.getInvestList(val);
				}
			},
			//条件搜索请求数据
			toSearch() {
				// console.log(this.searchText);
				this.getInvestList(1);
			},
			//修改条件时请求
			termSearch(name, index, id, type) {
				// console.log(111);
				if(type == 'alert') {
					this.addaccountDialogVisible1 = false;
				}
				if(this.termArr[index] != name) {
					this.termArr.splice(index, 1, name);
					if(index == 0) {
						if(id != undefined) {
							this.termId = id;
						} else {
							this.termId = "";
						}
					}
					this.getInvestList(1);
				} else {
					// console.log("当前数据就是该条件下查找的");
				}
			},
			//删除该条件请求数据
			deleteTerm(index) {
				this.termArr.splice(index, 1, "不限");
				this.getInvestList(1);
			},
			//获取投资年份
			getInvestYear() {
				invest_year().then(res => {
					if(res.data.msgNo == 1) {
						// console.log(res.data.payload);
						this.dataArr[1].termList = res.data.payload;
					} else {
						dialogConfirm("info", res.data.message);
					}
				});
			},
			//获取投资分类
			getInvestCategory() {
				invest_category().then(res => {
					if(res.data.msgNo == 1) {

						this.sortList = res.data.payload
						this.sortList.forEach(element => {
							element.noChildArr = [];
							element.child.forEach((item, index) => {
								// console.log(item.category_name,item.child.length)
								// console.log(item.category_name)
								// console.log(item.child.length)
								if(item.child.length == 0) {
									// console.log(item.category_name);
									// console.log(item.category_name,item.child.length);
									element.noChildArr.push(item);
									// element.child.splice(index,1);
								} else {
									// console.log(item.category_name,item.child.length);
								}

								// if(item.category_name=='酒店'){
								//   console.log(item.child);
								// }
							});
						});
						//  console.log(this.sortList);
						this.dataArr[0].termList = res.data.payload;
					} else {
						dialogConfirm("info", res.data.message);
					}
				});
			},
			//获取投资列表
			getInvestList(val) {
				investList({
					search: this.searchText,
					year: this.termArr[1] == "不限" ? "" : this.termArr[1],
					industry_id: this.termArr[0] == "不限" ? "" : this.termId,
					pageSize: this.pageSize,
					page: val
				}).then(res => {
					// console.log(res);
					if(res.data.msgNo == 1) {
						this.currentPage = val;
						this.total = res.data.payload.total;
						this.dataList = res.data.payload.data;
					} else {
						dialogConfirm("info", res.data.message);
					}
				});
			},
			toAlertMore() {
				// console.log(11111);
				this.addaccountDialogVisible1 = true;
			},
			toClearSearchText() {
				this.searchText = "";
				this.toSearch();
			},
			goBack(name) {
				this.$router.push({
					path: name
				});
			},
			toCancleConfirm1() {
				this.addaccountDialogVisible1 = false;
			},
			// toGetResult(name, index, id) {
			//   if (this.termArr[index] != name) {
			//     this.termArr.splice(index, 1, name);
			//     if (index == 0) {
			//       if (id != undefined) {
			//         this.termId = id;
			//       } else {
			//         this.termId = "";
			//       }
			//     }
			//     this.getInvestList(1);
			//   } else {
			//     // console.log("当前数据就是该条件下查找的");
			//   }
			// },
		}
	};
</script>

<style lang="scss" scoped>
	@import "../../style/modal.scss";
	.title_name {
		margin: 0px 0px 15px 0px;
		//   padding-left: 5px;
		font-size: 18px;
		color: #35383A;
	}
	
	.table-content {
		padding: 20px 30px;
	}
	
	.table-content-title {
		margin: 0;
	}
	
	.investmentEvent-content-box {
		width: 100%;
		min-height: 500px;
		border: 1px solid #e4e4e4;
		border-top-left-radius: 7px;
		border-top-right-radius: 7px;
	}
	
	.investmentEvent-term-box {
		width: 100%;
		// height: 400px;
		min-height: 400px;
		background: url("../../images/investevent-term-bg.png") center center no-repeat;
		background-size: cover;
		filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(src='../../images/investevent-term-bg.png', sizingMethod='scale');
		padding: 26px 50px 100px 50px;
	}
	
	.term-line-box {
		display: flex;
		align-items: baseline;
		font-size: 14px;
		margin-bottom: 30px;
		span {
			color: #ffffff;
		}
		.term-line-box-left {
			width: 66px;
		}
		.search-size {
			font-size: 18px;
		}
		.term-line-box-right {
			display: flex;
			flex-wrap: wrap;
			position: relative;
			.iconfont {
				position: relative;
				left: -70px;
				top: 8px;
				color: #a0a4aa;
				cursor: pointer;
			}
			input {
				width: 200px;
				height: 32px;
				border: 1px solid #e4e4e4;
				text-indent: 10px;
			}
			.search-btn {
				width: 44px;
				height: 32px;
				background-color: #1787fb;
				cursor: pointer;
				display: flex;
				justify-content: center;
				align-items: center;
				img {
					width: 20px;
					height: 20px;
				}
			}
			.selcet-item {
				cursor: pointer;
				height: 25px;
				padding: 5px 12px;
				display: flex;
				align-items: center;
				margin: 0px 20px 5px 0px;
			}
			.selcet-item:hover {
				border: 1px solid #ffffff;
				background-color: rgba(255, 255, 255, 0.2);
				border-radius: 13px;
				padding: 4px 11px;
			}
			.selcet-item-click {
				border: 1px solid #ffffff;
				background-color: rgba(255, 255, 255, 1);
				border-radius: 13px;
				padding: 4px 11px;
				color: #3c98e8;
			}
			.selcet-item-click:hover {
				border: 1px solid #ffffff;
				background-color: rgba(255, 255, 255, 1);
				border-radius: 13px;
				padding: 4px 11px;
				color: #3c98e8;
			}
		}
		.term-line-box-right-2 {
			width: calc(100% - 200px);
		}
	}
	
	.search-result-box {
		padding: 22px 20px;
		width: calc(100% - 60px);
		margin-left: 30px;
		position: relative;
		top: -100px;
		background-color: #ffffff;
		-webkit-box-shadow: 0 0 20px rgba(215, 221, 231, 0.9);
		-moz-box-shadow: 0 0 20px rgba(215, 221, 231, 0.9);
		box-shadow: 0 0 20px rgba(215, 221, 231, 0.9);
		border-radius: 3px;
	}
	
	.search-result-box-title {
		font-size: 14px;
		color: #35383a;
		display: flex;
		align-items: center;
		.result-txt {
			font-size: 16px;
			font-weight: 500;
		}
		.result-number {
			font-size: 16px;
			font-family: Georgia;
			font-weight: bold;
			color: #3c98e8;
		}
		.result-term {
			margin-left: 36px;
		}
		.result-term-item {
			padding: 2px 6px;
			height: 22px;
			display: flex;
			align-items: center;
			background-color: #3c98e8;
			margin-right: 10px;
			cursor: pointer;
			span {
				color: #ffffff;
				font-size: 14px;
			}
			img {
				width: 8px;
				height: 8px;
				margin-left: 4px;
			}
		}
	}
	
	.search-result-box-items {
		.search-result-box-item,
		.search-result-box-item-title {
			display: flex;
			align-items: center;
			border-bottom: 1px solid #f1f1f1;
			padding: 17px 0px;
		}
		.search-result-box-item-title {
			border-bottom: 2px solid #f1f1f1;
			padding: 17px 0px 8px 0px;
			.firstSpan,
			.firstSpan1 {
				font-size: 16px;
				color: #35383a;
				font-weight: 500;
			}
			.firstSpan1 {
				width: 60px;
				height: 30px;
				display: flex;
				align-items: center;
				padding-left: 9px;
			}
		}
		.search-result-box-item:nth-child(odd) {
			background-color: #f9fcff;
		}
		.search-result-box-item-col {
			width: 25%;
			display: flex;
			flex-direction: column;
			padding-right: 30px;
			.firstSpan {
				font-size: 16px;
				color: #35383a;
			}
			.secondSpan {
				font-size: 12px;
				color: #a0a4aa;
				padding-top: 6px;
			}
			.tablelink {
				color: #3c98e8;
				cursor: pointer;
			}
			.noData {
				color: #a0a4aa;
			}
		}
		.search-result-box-item-tzf {
			width: 25%;
		}
		.search-result-box-item-xq {
			width: 10%;
			.firstSpan {
				width: 60px;
				height: 30px;
				display: flex;
				align-items: center;
				justify-content: center;
				color: #616e82;
			}
			.firstSpan:hover {
				background-color: #3c98e8;
				color: #ffffff;
				cursor: pointer;
				border-radius: 2px;
			}
		}
	}
	
	.list-nav {
		// padding-left: 14px;
		margin-bottom: 12px;
	}
	
	.list-nav span {
		font-size: 14px;
		color: #3c98e8;
		cursor: pointer;
	}
	
	.list-nav span:last-child {
		color: #35383a;
		cursor: inherit;
	}
	
	.sort_level_1 {
		display: flex;
		flex-direction: column;
		border-bottom: 1px solid rgba(151, 151, 151, .3);
		padding-top: 15px;
		.sort_level_2 {
			display: flex;
			flex-wrap: wrap;
		}
		.sort_item {
			font-size: 16px;
			color: #35383a;
			margin-right: 25px;
