			color: #3c98e8;
		}
		.sort_item_two_level {
			color: #3c98e8;
			font-size: 16px;
		}
	}
	
	.sort_level_1:last-child {
		border: none;
	}
	.table-nodata{
		height: 300px;
	}
</style>


